.page-ready {

	.preloader {
		opacity: 0;
		visibility: hidden;
	}

	.header {
		transform: translateY(0);
		opacity: 1;
		transition-delay: .5s;
	}

	.page__column {
		opacity: 1;
		transition: .4s all;
		transition-delay: .5s;
	}

	.section--banner .banner__decor {
		opacity: 1;
	}
}


.header {
	transform: translateY(-15px);
	opacity: 0;
	transition: .4s all;
}

.page__column {
	opacity: 0;
}

.section--banner .banner__decor {
	opacity: 0;
	transition: .6s all;
	transition-delay: .6s;
}

.section--banner .js-animate {
	animation-delay: .6s;
}

.js-animate {
	//transform: translateY(30px);
	opacity: 0;
	//transition: .3s all;

	&.animated {
		//opacity: 1;
		//transform: translateY(0);
	}
}

.animated {
	animation-duration: 1s;
	animation-fill-mode: both;
}

.fadeInUp {
	animation-name: fadeInUp;
}

.fadeIn {
	animation-name: fadeIn;
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}

@keyframes fadeInUp {
	from {
		opacity: 0;
		transform: translate3d(0, 50%, 0);
	}

	to {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}
