button {
	background-color: transparent;
	border-radius: 0;
	border: 0;
	display: inline-block;
}

.btn {
	position: relative;
	display: inline-block;
	border-radius: 0;
	border: 0;
	transition: .3s all;
	background-color: transparent;
}

.btn-primary {
	background-color: $primary-color;
	font-weight: 700;
	line-height: 16px;
	font-size: 16px;
	color: #fff;

	&:hover {
		color: #fff;
		background-color: $primary-color-lighten;
	}
}

.btn-secondary {

	&:hover {

	}
}

.btn-uppercase {
	text-transform: uppercase;
}

.btn-text {
	@include fz-lg();
	padding: 4px 0 0;

	@include hoverEl();
}


.btn-link-arrow {
	@include fz-lg();

	&:hover {
		color: $primary-color;

		.arrow {
			width: 83px;
			background-color: $primary-color;

			&:after,
			&:before {
				background-color: $primary-color;
			}
		}
	}

	span.arrow {
		width: 59px;
		height: 4px;
		margin-top: -1px;
		background-color: #000;
		display: inline-block;
		vertical-align: middle;
		margin-left: 19px;
		position: relative;
		transition: .3s all;

		@include r($lg-breakpoint) {
			margin-left: 11px;
		}

		&:after,
		&:before {
			content: '';
			position: absolute;
			width: 15px;
			height: 4px;
			background-color: #000;
			transition: .3s all;
		}

		&:before {
			top: -4.5px;
			right: -4px;
			transform: rotate(45deg);
		}

		&:after {
			top: 4px;
			right: -4px;
			transform: rotate(-45deg);
		}
	}
}

.btn-burger {

	&.is-active {
		&:before {
			content: '';
			position: absolute;
			background-image: url('../img/burger-active.svg');
			background-position: right center;
			background-repeat: no-repeat;
			width: 16px;
			height: 17px;
			top: 50%;
			transform: translateY(-50%);
			right: 0;
		}

		.bar {
			opacity: 0;
		}
	}

	.bar {
		display: block;
		height: 2px;
		background-color: #000;
		width: 18px;
		margin-left: auto;

		&:not(:first-child) {
			margin-top: 4px;
			width: 32px;
		}
	}
}

.btn-border {
	background-color: transparent;
	color: #fff;
	font-weight: 700;
	line-height: 26px;
	font-size: 26px;
	text-align: center;
	border: 2px solid #fff;
	border-radius: 100px;
	padding: 20px 56px;

	@include r($lg-breakpoint) {
		padding: 12.5px 42px;
		line-height: 25px;
		font-size: 22px;
	}

	@include r($xs-breakpoint) {
		padding: 12.5px 28px;
	}

	&:hover {
		background-color: #fff;
	}
}
