.section--callback {
	padding: 106px 0;

	@include r($lg-breakpoint) {
		padding: 78px 0;
	}

	@include r($md-breakpoint) {
		padding: 70px 0;
	}

	@include r($sm-breakpoint) {
		padding: 75px 0;
	}

	@include r($xs-breakpoint) {
		padding: 54px 0;
	}
}

.callback {
	display: flex;
	justify-content: space-between;

	@include r($md-breakpoint) {
		display: block;
	}

	&__left {
		width: percentage(500/1600);

		@include r($md-breakpoint) {
			width: 100%;
		}

		.section__title-sm {

			@include r($md-breakpoint) {
				max-width: 787px;
			}

			@include r($sm-breakpoint) {
				max-width: 384px;
			}
		}

		.phone {
			margin: 78px 0 33px;

			@include r($lg-breakpoint) {
				margin: 48px 0 17px;
			}

			@include r($md-breakpoint) {
				margin: 0;
			}
		}

		.mail {

			@include r($md-breakpoint) {
				margin: 0 53px 0 48px;
			}

			@include r($xs-breakpoint) {
				margin: 20px 0 0;
			}
		}

		.socials {
			margin-top: 63px;

			@include r($lg-breakpoint) {
				margin-top: 30px;
			}

			@include r($md-breakpoint) {
				margin-top: 0;
			}

			@include r($sm-breakpoint) {
				margin-top: 20px;
			}

			@include r($xs-breakpoint) {
				margin-top: 30px;
				width: 100%;
			}
		}
	}

	&__right {
		width: percentage(790/1600);
		margin-right: 136px;
		padding-top: 98px;

		@include r($lg-breakpoint) {
			margin-right: 0;
			padding-top: 70px;
			width: percentage(623/1080);
		}

		@include r($md-breakpoint) {
			padding-top: 0;
			width: 100%;
			margin-top: 48px;
			max-width: 732px;
		}

		@include r($xs-breakpoint) {
			margin-top: 32px;
		}
	}

	&__contacts {


		@include r($md-breakpoint) {
			display: flex;
			align-items: center;
			flex-flow: wrap;
			margin-top: 48px;
		}

		@include r($xs-breakpoint) {
			margin-top: 25px;
		}
	}
}

.phone {

	a {
		@include h3();
		@include hoverEl();
		
		@include r($xs-breakpoint) {
			line-height: 41px;
			font-size: 32px;
		}

	}
}

.form__textarea {
	display: flex;
}


.mail {

	a {
		@include h4();

		@include hoverEl();
	}
}

.socials {
	display: flex;
	flex-flow: wrap;
	margin: -10px -30px;

	@include r($lg-breakpoint) {
		margin: -10px -16px;
	}

	&__item {
		padding: 10px 30px;

		@include r($lg-breakpoint) {
			padding: 10px 16px;
		}

		a {
			@include h5();
		}
	}
}

.behance:hover {
	color: #3C51F7;
}

.instagram {
	position: relative;
	display: block;

	&:hover {
		color: inherit;

		.state-1 {
			opacity: 0;
		}

		.state-2 {
			opacity: 1;
		}
	}

	> * {
		position: relative;
		transition: .3s all;
		display: block;
	}


	.state-2 {
		position: absolute;
		top: 50%;
		left: 0;
		margin-top: -12px;
		opacity: 0;
		background: linear-gradient(to right, rgba(255,167,15,1) 0%,rgba(226,6,8,1) 50%,rgba(218,0,209,1) 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}
}

.callback__brief {
	margin-top: 62px;

	@include r($lg-breakpoint) {
		margin-top: 40px;
	}

	@include r($md-breakpoint) {
		margin-top: 22px;
	}

	@include r($sm-breakpoint) {
		margin-top: 15px;
	}

	@include r($xs-breakpoint) {
		margin-top: 26px;
	}

	a {
		color: $primary-color;
		@include h4();

		display: inline-block;

		&:hover {

			span {
				background-size: .9em 1.5em;
				color: #fff;
			}
		}

		span {
			padding: 5px 0;
			transition: .3s all;
			background-image: linear-gradient($primary-color,$primary-color);
			background-repeat: repeat-x;
			background-size: 0 0;
			background-position: 0 65%;
		}
	}
}
