.page__column--left {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;

	@include r($xs-breakpoint) {
		display: none;
	}

	.nav {
		//top: 434px;
		//left: -345px;
		position: absolute;
		top: 0;
		left: 115px;
		//position: relative;
		transform: rotate(90deg);
		width: 100vh;
		transform-origin: 0 0;
		padding-left: 150px;
		padding-right: 104px;

		@include r($lg-breakpoint) {
			left: 108px;
			padding-left: 130px;
			padding-right: 65px;
		}

		@include r($md-breakpoint) {
			left: 85px;
		}

		ul {
			display: flex;
			align-items: center;
			justify-content: space-between;
			//justify-content: center;
			position: relative;
			overflow: hidden;
			padding-left: 35px;

			@include r($lg-breakpoint) {
			}

			li:not(:first-child) {
				//margin-left: 62px;
				//margin-right: -30px;

				//@include r($lg-breakpoint) {
				//	margin-left: 32px;
				//	margin-right: 0;
				//}
			}

			li.is-active {
				position: relative;
				//margin-right: auto;
				//overflow: hidden;
				//margin-right: auto;
				margin-right: 190px;
				text-align: right;
				//width: percentage(313/619);

				a {
					color: $primary-color;
					overflow: visible;

					.line {
						height: 200px;
					}

					&:before {
						height: 42vh;
						//height: 227px;
					}
				}
			}

			li {
				transform: rotate(180deg);
				position: relative;
				transition: .3s all;
				//width: percentage(107/619);

				a {
					position: relative;
					display: inline-block;
					padding-left: 32px;
					padding-right: 32px;
					//overflow: hidden;
					background-color: #fff;

					@include r($lg-breakpoint) {
						padding-left: 5px;
						padding-right: 16px;
					}

					span {
						position: absolute;
						top: 50%;
						right: 100%;
						width: 3px;
						background-color: $primary-color;
						transform: rotate(90deg);
						margin-top: -3px;
						transition: .3s all;
						transform-origin: 0 0;
						display: block;
					}
				}
			}

			a {
				white-space: nowrap;
			}
		}
	}
}
