.section--error {
	@include cover();
	color: #fff;

	.error {
		min-height: 100vh;
	}
}

.error {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 0;

	@include r($sm-breakpoint) {
		display: block;
	}

	&__left {
		max-width: 608px;

		@include r($md-breakpoint) {
			min-width: 462px;
			margin-top: -106px;
		}

		@include r($sm-breakpoint) {
			padding-top: 135px;
			margin-top: 0;
			max-width: 462px;
			min-width: auto;
		}

		@include r($xs-breakpoint) {
			padding-top: 83px;
		}
	}

	&__right {
		margin-right: -28px;
		margin-top: -20px;
		pointer-events: none;

		@include r($lg-breakpoint) {
			margin-right: -30px;
			margin-top: -20px;
		}

		@include r($md-breakpoint) {
			margin-right: 0;
			margin-top: 0;
			position: relative;
			left: -190px;
			top: 18px;
		}

		@include r($sm-breakpoint) {
			top: 0;
			margin-top: -70px;
			left: 35px;
		}

		@include r($xs-breakpoint) {
			margin-top: -105px;
			left: 53px;
			text-align: right;
		}

		img {
			max-width: 579px;

			@include r($xs-breakpoint) {
				max-width: 240px;
			}
		}
	}

	&__title {
		@include h1();
	}
	
	&__text {
		@include fz-text-sm();
		margin: 56px 0 75px;

		@include r($lg-breakpoint) {
			margin: 21px 0 58px;
		}

		@include r($xs-breakpoint) {
			line-height: 26px;
			font-size: 18px;
			margin-bottom: 35px;
		}
	}
}
