.menuBox {
	background-color: #F6F6F6;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	height: 100vh;
	z-index: 20;
	padding-top: 75px;
	opacity: 0;
	visibility: hidden;
	transition: .3s all;

	&.is-active {
		opacity: 1;
		visibility: visible;
	}


	.menu {


		li:not(:first-child) {
			margin-top: 15px;
		}

		a {
			line-height: 41px;
			font-size: 32px;
			font-weight: 700;

			&.is-active {
				color: #502DFC;
			}
		}
	}

	.callback__contacts {
		margin-top: 37px;

		.mail {
			margin: 3px 0 50px;
			width: 100%;
		}

		a {
			font-size: 24px;
			font-weight: 600;
		}

		.socials {
			width: 100%;
		}

		.socials a {
			line-height: 23px;
			font-size: 14px;
			letter-spacing: 0.05em;
		}
	}

}
