.cookie {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background-color: #000;
	color: #fff;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	@include fz-text-sm();
	padding: 16px 0;
	transition: .3s all;
	transform: translateY(-100%);
	opacity: 0;
	visibility: hidden;
	z-index: 20;

	@include r($lg-breakpoint) {
		padding: 8px 0;
	}

	@include r($sm-breakpoint) {
		padding: 10px 72px;
		text-align: left;
	}

	@include r($xs-breakpoint) {
		padding: 15px 17px 0;
		display: block;
	}

	a:not(.btn) {
		color: #fff;
		font-weight: 500;

		&:hover {
			color: $primary-color;
		}
	}

	&__button {
		margin-left: 43px;

		@include r($xs-breakpoint) {
			margin: 15px -17px 0;
		}
	}

	.btn {
		min-width: 165px;
		padding: 12px 0;
		text-align: center;

		@include r($xs-breakpoint) {
			width: 100%;
		}
	}
}
