.section--start {
	padding: 108px 0 140px;

	@include r($lg-breakpoint) {
		padding: 80px 0 113px;
	}
	
	@include r($sm-breakpoint) {
		padding: 80px 0 88px;
	}

	@include r($xs-breakpoint) {
		padding: 55px 0 78px;
	}

	.section__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;

		@include r($md-breakpoint) {
			align-items: flex-start;
		}

		@include r($sm-breakpoint) {
			display: block;
		}
	}

	.section__text {
		margin-top: 50px;

		@include r($lg-breakpoint) {
			margin-top: 33px;
		}

		@include r($xs-breakpoint) {
			margin-top: 38px;
		}
	}

	.section__left {
		width: percentage(1056/1600);

		@include r($sm-breakpoint) {
			width: 100%;
		}
	}

	.section__right {
		width: percentage(521/1600);
		margin-top: 120px;

		@include r($lg-breakpoint) {
			margin-top: 70px;
		}

		@include r($md-breakpoint) {
			margin-top: 160px;
		}

		@include r($sm-breakpoint) {
			width: 100%;
			margin-top: 48px;
			text-align: center;
		}

		@include r($xs-breakpoint) {
			margin-top: 38px;
			padding: 0 16px;
		}
	}
}
