.section--aboutPreview {
}

.aboutPreview {
	border-top: 1px solid #C6C6C6;
	padding: 106px 0 103px;

	@include r($lg-breakpoint) {
		padding: 78px 0 87px;
	}

	@include r($md-breakpoint) {
		padding: 78px 0 88px;
	}

	@include r($sm-breakpoint) {
		padding: 78px 0 88px;
	}

	@include r($xs-breakpoint) {
		padding: 54px 0 63px;
	}

	.section__text {
		margin-top: 52px;
		max-width: percentage(1196/1600);

		@include r($lg-breakpoint) {
			margin-top: 34px;
		}

		@include r($md-breakpoint) {
			max-width: 732px;
		}

		@include r($xs-breakpoint) {
			margin-top: 38px;
		}
	}

	.servicesList {
		margin-top: 127px;

		@include r($lg-breakpoint) {
			margin-top: 80px;
		}

		@include r($xs-breakpoint) {
			margin-top: 60px;
		}
	}
}
