.widget {
	position: fixed;
	bottom: 40px;
	right: 40px;
	z-index: 20;

	@include r($lg-breakpoint) {
		right: 20px;
	}

	@include r($xs-breakpoint) {
		display: none;
	}

	&.is-active {

		.widget__body {
			display: block;
			animation: fadeIn .3s forwards ease-in-out;
		}

		.widget__toggle {
			background-color: #fff;
			box-shadow: 0px 2px 10px rgba(80, 45, 252, 0.1);

			&:hover {

				svg rect{
					fill: $primary-color;
				}
			}

			.state--active {
				opacity: 1;
				visibility: visible;
			}

			.state--default {
				opacity: 0;
				visibility: hidden;
			}
		}
	}

	&__head {
		margin-bottom: 23px;
	}

	&__icon {

		a {
			width: 60px;
			height: 60px;
			background-color: $primary-color;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;

			&:hover {
				background-color: #000000;
			}

			.state {
				transition: .3s all;
			}

			svg {
				position: relative;
				top: 3px;

				rect {
					transition: .3s all;
				}
			}

			.state--active {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				opacity: 0;
				visibility: hidden;
			}
		}
	}

	&__body {
		position: absolute;
		bottom: 100%;
		background-color: #fff;
		box-shadow: 0px 2px 10px rgba(80, 45, 252, 0.1);
		right: 0;
		margin-bottom: 17px;
		padding: 40px 27px 31px 20px;
		min-width: 418px;
		display: none;
	}

	&__title {
		line-height: 25px;
		font-size: 22px;
		font-weight: 700;
	}

	&__subtitle {
		line-height: 26px;
		font-size: 18px;
		margin-top: 8px;
	}
}

.widgetSocials {
	margin: -4px;
	display: flex;
	flex-flow: wrap;

	&__item {
		padding: 4px;
		width: 50%;
	}
}


.social {
	border-radius: 4px;
	color: #fff;
	font-weight: 600;
	line-height: 26px;
	font-size: 18px;
	height: 48px;
	position: relative;
	display: flex;
	align-items: center;
	padding-left: 65px;
	overflow: hidden;
	
	&:hover {
		color: #fff;
		padding-left: 65px + 6px;

		.social__icon {
			width: 56px;
		}
	}

	&.social--whatsapp {
		background: #1CCB6C;

		.social__icon {
			background-color: #1FD773;
		}
	}

	&.social--email {
		background: #387CED;

		.social__icon {
			background-color: #3B82F9;
		}
	}

	&.social--telegram {
		background: #338CBF;

		.social__icon {
			background-color: #3798D0;
		}
	}

	&.social--vk {
		background: #5980B0;

		.social__icon {
			background-color: #6188B9;
		}
	}

	&__icon {
		width: 48px;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		transition: .25s all;

		svg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}


@keyframes fadeIn {
	from {
		opacity: 0;
		transform: translateY(10px);
	}

	to {
		opacity: 1;
		transform: translateY(0);
	}
}
