.work__block--banner {
	padding-top: 82px;
	min-height: 690px;

	@include r($lg-breakpoint) {
		padding-top: 97px;
		min-height: 603px;
	}

	@include r($md-breakpoint) {
		padding-top: 42px;
		min-height: 488px;
	}

	@include r($xs-breakpoint) {
		padding-top: 0;
		min-height: 450px;
	}

	.banner {
		position: relative;
		z-index: 2;
	}


	.banner__image {
		position: absolute;
		bottom: 0;
		right: 0;
		pointer-events: none;

		@include r($lg-breakpoint) {
			max-width: 902px;
		}

		@include r($md-breakpoint) {
			max-width: 742px;
		}

		@include r($sm-breakpoint) {
			position: relative;
			max-width: 648px;
			margin-left: auto;
			margin-top: -66px;
		}

		@include r($xs-breakpoint) {
			margin-top: -27px;
		}
	}
	
	.banner__title {
		
		@include r($xs-breakpoint) {
			line-height: 40px;
			font-size: 40px;
		}
	}

	.worksList {
		margin-top: 46px;
		padding-left: 5px;

		@include r($sm-breakpoint) {
			padding-left: 0;
		}

		@include r($xs-breakpoint) {
			margin-top: 28px;
		}
	}

	.date {
		margin-top: 35px;
		padding-left: 5px;

		@include r($sm-breakpoint) {
			padding-left: 0;
		}

		@include r($xs-breakpoint) {
			margin-top: 25px;
			font-size: 14px;
		}
	}
}

.worksList {
	@include h5();
}
