.work__block.work__block--description {
	padding-top: 172px;
	padding-bottom: 160px;

	@include r($lg-breakpoint) {
		padding-top: 128px;
		padding-bottom: 128px;
	}

	@include r($sm-breakpoint) {
		padding-top: 68px;
		padding-bottom: 80px;
	}

	@include r($xs-breakpoint) {
		padding-top: 53px;
		padding-bottom: 55px;
	}
}

.description {
	display: flex;
	justify-content: space-between;
	padding: 0 82px 0 0;

	@include r($md-breakpoint) {
		padding-right: 0;
	}

	@include r($sm-breakpoint) {
		display: block;
	}

	&__left {
		width: percentage(458/1080);

		@include r($sm-breakpoint) {
			width: 100%;
		}
	}

	&__right {
		width: percentage(500/1080);
		
		@include r($md-breakpoint) {
			width: percentage(532/1080);
		}

		@include r($sm-breakpoint) {
			width: 100%;
			margin-top: 43px;
		}

		@include r($xs-breakpoint) {
			margin-top: 40px;
		}
	}

	&__title {
		@include h2();
	}

	&__text {
		@include fz-text();
	}
}
