@import "banner";
@import "dialog";
@import "description";
@import "footer";


.work__block {
	padding: 0 136px;
	position: relative;

	@include r($lg-breakpoint) {
		padding-left: 0;
		padding-right: 0;
	}

	&.work__block--full {
		padding: 0;
	}

	.text-center {

		@include rmin($lg-breakpoint) {
			padding-left: 0;
			padding-right: 0;
		}

		@extend .container;
	}
}

.double {
	display: flex;
	justify-content: space-between;
	padding-bottom: 164px;

	@include r($lg-breakpoint) {
		padding-bottom: 128px;


		.double__item:first-child {
			max-width: 417px;
		}

		.double__item:last-child {
			max-width: 532px;
		}
	}

	@include r($lg-breakpoint) {
		padding-bottom: 97px;


		.double__item:first-child {
			max-width: 312px;
		}

		.double__item:last-child {
			max-width: 392px;
		}
	}

	@include r($sm-breakpoint) {
		padding-bottom: 130px;
		display: block;


		.double__item:first-child {
			max-width: 388px;
		}

		.double__item:last-child {
			max-width: 588px;
		}
	}

	@include r($sm-breakpoint) {
		padding-bottom: 44px;
		display: block;


		.double__item:first-child {
			max-width: 182px;
		}

		.double__item:last-child {
			max-width: 288px;
		}
	}
}

.work__block--full {

	.btn-link-arrow {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		height: 100px;
		
		&:hover {
			color: #000;
			background: #27BD31;

			span.arrow {
				background: #000;

				&:before,
				&:after {
					background: inherit;
				}
			}
		}
	}
}
