@import "grid";

.container-fluid,
.container {
	width: 100%;
	padding: 0 95px;
	margin: 0 auto;
	position: relative;

	@include r($lg-breakpoint) {
		padding: 0 93px;
	}

	@include r($md-breakpoint) {
		padding: 0 73px;
	}

	@include r($sm-breakpoint) {
		padding: 0 65px;
	}

	@include r($xs-breakpoint) {
		padding: 0 16px;
	}
}

.container {
	padding-left: 200px;
	padding-right: 100px;

	@include r($lg-breakpoint) {
		padding-left: 180px;
		padding-right: 100px;
	}

	@include r($md-breakpoint) {
		padding-left: 140px;
		padding-right: 80px;
	}

	@include r($sm-breakpoint) {
		padding-left: 120px;
		padding-right: 60px;
	}
}

.color-black {
	color: $black;
}

.title-lg {

}
.title-sm {

}
.title-md {

}

.text-center {
	text-align: center;
}

.full-vp {
	height: 100vh;
}

.flex {
	display: flex;
}

.flex-wrap {
	display: flex;
	flex-flow: wrap;
}

.flex-vbottom {
	display: flex;
	align-items: flex-end;
}

.flex-vcenter {
	display: flex;
	align-items: center;
}

.flex-hcenter {
	display: flex;
	justify-content: center;
}

.flex-vhcenter {
	display: flex;
	justify-content: center;
	align-items: center;
}

.hidden-mobile {
	@include r($mob) {
		display: none;
	}
}

.hidden-desktop {

	@include rmin($mob) {
		display: none;
	}
}

.color-primary {
	color: $primary-color;
}

.col-1 {
	width: percentage(133/1600);
	padding: 0 8px;
}

.col-3 {
	width: percentage(399/1600);
	padding: 0 8px;
}

.col-6 {
	width: percentage(798/1600);
	padding: 0 8px;
}


.col-offest-left-1 {
	margin-left: percentage(133/1600);
}

.col-offest-left-2 {
	margin-left: percentage(266/1600);
}

.col-offest-left-3 {
	margin-left: percentage(399/1600);
}

.hidden-desk {

	@include rmin($sm-breakpoint) {
		display: none;
	}
}

.hidden-tablet {

	@include r($sm-breakpoint) {
		display: none;
	}
}
