.section--portfolio {
	padding-top: 81px;

	@include r($lg-breakpoint) {
		padding-top: 52px;
	}

	@include r($md-breakpoint) {
		padding-top: 48px;
	}

	@include r($xs-breakpoint) {
		padding-top: 0;
	}

	.section__head {
		padding-right: 30px;
		display: flex;
		justify-content: space-between;

		@include r($lg-breakpoint) {
			padding-right: 5px;
		}

		@include r($md-breakpoint) {
			padding-right: 0;
		}

		@include r($sm-breakpoint) {
			display: block;
		}

		.section__text {
			max-width: 537px;
			margin-top: 61px;

			@include r($lg-breakpoint) {
				max-width: 373px;
				line-height: 26px;
				font-size: 18px;
				margin-top: 50px;
			}

			@include r($md-breakpoint) {
				margin-top: 65px;
				max-width: 420px;
			}

			@include r($sm-breakpoint) {
				margin-left: auto;
				margin-right: 12px;
				margin-top: 35px;
				max-width: 384px;
			}

			@include r($xs-breakpoint) {
				margin-top: 23px;
			}
		}
	}

	.portfolio {
		margin-top: 57px;

		@include r($lg-breakpoint) {
			margin-top: 26px;
		}

		@include r($md-breakpoint) {
			margin-top: 58px;
		}

		@include r($sm-breakpoint) {
			margin-top: 80px;
		}

		@include r($xs-breakpoint) {
			margin-top: 35px;
		}
	}
}

.portfolio {

	&__nav {
		//padding-left: 68px;

		//@include r($lg-breakpoint) {
		//	padding-left: 18px;
		//}

		//@include r($md-breakpoint) {
		//	padding-left: 12px;
		//}

		//@include r($sm-breakpoint) {
		//	padding-left: 32px;
		//}

		@include r($xs-breakpoint) {
			height: 23px;
			overflow: hidden;
			margin: 0 -16px;
			//padding: 0;
		}

		ul {
			display: inline-block;
			
			@include r($xs-breakpoint) {
			    white-space: nowrap;
				overflow-y: auto;
				max-width: 100vw;
				padding: 0 16px;
				padding-bottom: 50px;
			}

			li {
				display: inline-block;

				&:not(:first-child) {
					margin-left: 34px;

					@include r($lg-breakpoint) {
						margin-left: 30px;
					}

					@include r($xs-breakpoint) {
						margin-left: 47px;
					}
				}
			}
		}

		a {
			text-transform: uppercase;
			line-height: 26px;
			font-size: 16px;
			letter-spacing: 0.05em;
			font-weight: 700;
			position: relative;
			display: inline-block;
			
			&:before {
				content: '';
				position: absolute;
				bottom: 0;
				right: 0;
				width: 0;
				background-color: #000;
				height: 2px;
				transition: .3s all ease-in-out;
			}

			&:hover {
				color: inherit;

				&:before {
					left: 0;
					right: auto;
					width: 100%;
				}
			}

			&.is-active {
				&:before {
					width: 100%;
				}
			}

			@include r($lg-breakpoint) {
				line-height: 23px;
				font-size: 14px;
			}
		}
	}

	&__grid {
		margin-top: 103px;

		@include r($lg-breakpoint) {
			margin-top: 80px;
		}

		@include r($md-breakpoint) {
			margin-top: 85px;
		}

		@include r($sm-breakpoint) {
			margin-top: 93px;
		}

		@include r($xs-breakpoint) {
			margin-top: 37px;
		}
	}

	&__row {
		@include clr();
		margin-left: -8px;
		margin-right: -8px;

		@include r($lg-breakpoint) {
			margin-bottom: 8px;
		}

		@include r($md-breakpoint) {
			margin-bottom: 30px;
			margin-left: 0;
			margin-right: 0;
		}

		@include r($sm-breakpoint) {
			margin-bottom: 16px;
		}

		> div {
			float: left;
			margin-bottom: 58px;
			opacity: 0;
			visibility: hidden;
			transform: translateY(15px);
			transition: .3s all;

			&.is-visible {
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
			}

			@include r($lg-breakpoint) {
				margin-bottom: 14px;
			}

			@include r($md-breakpoint) {
				margin-bottom: 5px;
			}

			@include r($sm-breakpoint) {
				margin-left: 0;
				margin-bottom: 14px;
				padding: 0;

				&:not(.card--lg) {
					width: 182px;
				}

				&.card--lg {
					width: 336px;
				}
			}

			@include r($xs-breakpoint) {
				width: 100% !important;
				margin-bottom: 11px;
			}
		}

		> div.card--lg:not(:first-child) {
			float: right;
		}

		> div.card--lg:first-child + div,
		> div.card--lg:first-child + div + div{
			
			@include r($sm-breakpoint) {
				float: right;
			}
		}
	}
}



