.case {
	text-align: center;
	display: inline-block;
	position: relative;

	&:hover {

		.case__action {
			opacity: 1;
		}
	}

	&__link {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 3;
	}

	&__preview {
		@include cover();
		width: 388px;
		height: 270px;
		position: relative;

		@include r($lg-breakpoint) {
			width: 258px;
			height: 184px;
		}

		@include r($sm-breakpoint) {
			width: 286px;
			height: 200px;
		}

		@include r($xs-breakpoint) {
			width: 174px;
			height: 120px;
		}

		> div {
			height: 100%;
			@include cover();
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
		}

		.case__action {
			opacity: 0;
			z-index: 2;
			background-color: $primary-color;
		}
	}

	&__descr {
		margin-top: 27px;
		@include fz-lg();
		padding-left: 40px;

		@include r($lg-breakpoint) {
			margin-top: 16px;
			padding-left: 58px;
		}

		@include r($md-breakpoint) {
			max-width: 100px;
			padding-left: 0;
			margin-left: auto;
			margin-top: 12px;
			text-align: left;
			margin-right: 20px;
		}

		@include r($sm-breakpoint) {
			margin-top: 10px;
			margin-right: 37px;
		}

		@include r($xs-breakpoint) {
			margin-right: 0;
			margin-top: -60px;
		}
	}
}
