.servicesList {
	display: flex;
	flex-flow: wrap;
	margin: 0 -16px;

	&__item {
		background-color: #fff;
		width: calc(33.33% - 32px);
		margin: 0 16px;
		padding: 36px 55px;

		@include r($lg-breakpoint) {
			padding: 32px 40px 39px;
		}

		@include r($md-breakpoint) {
			width: 100%;
			display: flex;
			padding: 32px 40px 38px;
			min-height: 192px;
		}
		
		@include r($xs-breakpoint) {
			display: block;
			padding: 24px 28px 33px;
			min-height: 224px;
		}

		&:not(:first-child) {

			@include r($md-breakpoint) {
				margin-top: 24px;
			}
		}
	}

	&__title {
		@include h3();

		@include r($md-breakpoint) {
			width: percentage(232/724);
		}
		
		@include r($sm-breakpoint) {
			width: percentage(301/724);
		}

		@include r($xs-breakpoint) {
			width: 100%;
			line-height: 41px;
			font-size: 32px;
		}
	}

	&__links {
		margin-top: 20px;
		padding-left: 5px;

		@include r($lg-breakpoint) {
			margin-top: 24px;
			padding-left: 0;
		}

		@include r($md-breakpoint) {
			margin-top: 0;
			padding-top: 10px;
			width: percentage(300/724);
		}
		
		@include r($sm-breakpoint) {
			width: percentage(360/724);
		}

		@include r($xs-breakpoint) {
			margin-top: 10px;
			width: 100%;
		}

		> li:not(:first-child) {
			margin-top: 11px;

			@include r($lg-breakpoint) {
				margin-top: 8px;
			}
			@include r($xs-breakpoint) {
				margin-top: 14px;
			}
		}

		a {
			@include fz-text();
		}
	}
}
