.work__block.work__block--footer {
	padding-top: 190px;
	padding-bottom: 178px;
	@include cover();

	@include r($lg-breakpoint) {
		padding-top: 134px;
		padding-bottom: 95px;
	}

	@include r($md-breakpoint) {
		padding-top: 95px;
		padding-bottom: 100px;
	}

	@include r($sm-breakpoint) {
		padding-top: 90px;
		padding-bottom: 70px;
	}

	@include r($xs-breakpoint) {
		padding-top: 51px;
		padding-bottom: 9px;
	}

	.creators {
		margin-top: 175px;
		padding: 0 250px 0 135px;

		@include r($lg-breakpoint) {
			padding: 0;
			margin-top: 115px;
		}

		@include r($md-breakpoint) {
			margin-top: 79px;
		}

		@include r($sm-breakpoint) {
			margin-top: 65px;
		}

		@include r($xs-breakpoint) {
			margin-top: 38px;
		}
	}
}

.finishBox {

	&__head {
		display: flex;
		flex-flow: column;
		justify-content: center;
		align-items: center;
	}

	&__title {
		line-height: 90px;
		font-size: 80px;
		text-align: center;
		font-weight: 900;
		letter-spacing: 0.5em;
		text-transform: uppercase;

		@include r($lg-breakpoint) {
			line-height: 90px;
			font-size: 60px;
		}

		@include r($sm-breakpoint) {
			line-height: 90px;
			font-size: 50px;
		}

		@include r($xs-breakpoint) {
			line-height: 1.2;
			font-size: 34px;
			position: relative;
			right: -10px;
		}
	}

	&__subtitle {
		line-height: 63px;
		font-size: 40px;
		text-align: center;
		letter-spacing: 0.5em;
		text-transform: uppercase;
		margin-top: 30px;

		@include r($lg-breakpoint) {
			line-height: 50px;
			font-size: 32px;
			margin-top: 17px;
		}

		@include r($sm-breakpoint) {
			line-height: 38px;
			font-size: 24px;
			margin-top: -5px;
		}

		@include r($xs-breakpoint) {
			line-height: 32px;
			font-size: 20px;
			margin-top: 10px;
		}
	}

}

.creators {
	display: flex;
	flex-flow: wrap;
	justify-content: space-between;

	&__item {

		@include r($sm-breakpoint) {
			width: 50%;

			&:nth-child(n+3) {
				margin-top: 30px;
			}
		}

		@include r($xs-breakpoint) {
			width: 100%;
			margin-bottom: 29px;
			text-align: center;

			&:nth-child(n+3) {
				margin-top: 0;
			}
		}
	}

	&__title {
		line-height: 26px;
		font-size: 26px;
		font-weight: 700;

		@include r($lg-breakpoint) {
			line-height: 25px;
			font-size: 22px;
		}
		
		@include r($xs-breakpoint) {
			line-height: 26px;
			font-size: 26px;
		}
	}

	&__subtitle {
		line-height: 31px;
		font-size: 21px;
		font-weight: 600;
		margin-top: 17px;

		@include r($lg-breakpoint) {
			line-height: 26px;
			font-size: 18px;
			margin-top: 22px;
		}

		@include r($sm-breakpoint) {
			margin-top: 12px;
		}

		@include r($xs-breakpoint) {
			margin-top: 10px;
		}
	}

}
