.section--banner {
	min-height: 100vh;
	//padding-top: 110px;
	//padding-top: 75px;
	padding-bottom: 110px;
	margin-top: -110px;
	display: flex;
	align-items: center;
	position: relative;

	@include r($lg-breakpoint) {
		//padding-top: 97px;
		padding-bottom: 97px;
		margin-top: -97px;
	}

	@include r($md-breakpoint) {
		//padding-top: 88px;
		padding-bottom: 88px;
		margin-top: -88px;
	}

	@include r($xs-breakpoint) {
		padding-top: 78px;
		padding-bottom: 37px;

	}
}

.banner {
	position: relative;

	&__inner {
		padding-top: 149px;
		//position: relative;
		display: inline-block;

		@include r($lg-breakpoint) {
			padding-top: 38px;
		}

		@include r($md-breakpoint) {
			padding-top: 55px;
		}

		@include r($sm-breakpoint) {
			padding-top: 26px;
		}

		@include r($xs-breakpoint) {
			display: flex;
			flex-flow: column;
			padding-top: 0;
		}

		> *:not(.banner__decor) {
			position: relative;
			z-index: 2;
		}
	}

	&__title {
		@include h1();
		max-width: 750px;

		@include r($sm-breakpoint) {
			max-width: 540px;
		}

		@include r($xs-breakpoint) {
			max-width: 343px;
		}

	}

	&__subtitle {
		max-width: 780px;
		@include fz-text();
		margin-top: 47px;

		@include r($lg-breakpoint) {
			max-width: 572px;
			margin-top: 50px;
		}

		@include r($md-breakpoint) {
			max-width: 422px;
		}

		@include r($sm-breakpoint) {
			margin-top: 380px;
			max-width: 100%;
		}

		@include r($xs-breakpoint) {
			margin-top: 32px;
		}
	}

	&__bottom {
		margin-top: 50px;

		@include r($lg-breakpoint) {
			margin-top: 55px;
		}

		@include r($sm-breakpoint) {
			margin-top: 80px;
		}

		@include r($xs-breakpoint) {
			margin-top: 33px;
		}
	}

	&__decor {
		position: absolute;
		top: -6px;
		right: 0;
		margin-right: 269px;
		width: 792px;
		height: 712px;
		@include cover();

		@include r($lg-breakpoint) {
			width: 535px;
			height: 472px;
			top: -9px;
			margin-right: 180px;
		}

		@include r($md-breakpoint) {
			width: 392px;
			height: 536px;
			margin-right: 136px;
		}

		@include r($sm-breakpoint) {
			margin-left: 0;
			top: 0;
			left: 0;
			position: relative;
			width: 437px;
			height: 448px;
			margin-top: -691px;
		}

		@include r($xs-breakpoint) {
			position: absolute;
			width: auto;
			height: 240px;
			margin-top: 0;
			top: 98px;
			left: -16px;
			right: -16px;
			margin-right: 0;
		}
	}

	.case {
		position: absolute;
		bottom: -110px;
		right: 0;
		
		@include r($lg-breakpoint) {
			bottom: -22px;
		}

		@include r($md-breakpoint) {
			bottom: -50px;
			right: 4px;
		}

		@include r($sm-breakpoint) {
			bottom: 245px;
			right: 0;
		}

		@include r($xs-breakpoint) {
			bottom: 0;
			position: relative;
			margin-top: 58px;
			max-width: 320px;
		}
	}
}
