@import "inputs";
@import "range";

.fieldBorder {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	height: 1px;
	background-color: $black;

	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 2px;
		background-color: $primary-color;
		opacity: 0;
		transition: .3s all;
	}
}

.form__textarea .fieldBorder {
	height: 100%;
	background-color: transparent;
	border: 1px solid $black;
	pointer-events: none;

	&:before {
		height: 100%;
		border: 2px solid $primary-color;
		background-color: transparent;
	}
}

.input {
	line-height: 30px;
	font-size: 21px;
	color: #000;
	padding-bottom: 5px;
	height: 36px;
	//border-bottom: 1px solid $black;

	@include r($lg-breakpoint) {
		line-height: 26px;
		font-size: 18px;
	}

	@include r($xs-breakpoint) {
		height: 35px;
	}

	&:hover + .fieldBorder:before {
		opacity: 1;
	}

	&:focus {

		+ .fieldBorder:before {
			opacity: 1;
		}

		&.active {

			.input {
				border-color: #502DFC
			}
		}
	}

	&[aria-invalid="true"] {
		border-color: $red;
	}
}

.textarea {
	line-height: 30px;
	font-size: 21px;
	color: #000;
	//border: 1px solid #000;
	resize: none;
	width: 100%;
	height: 216px;

	padding-top: 13px;
	padding-left: 17px;
	transition: .3s all;

	@include r($lg-breakpoint) {
		height: 144px;
		line-height: 26px;
		font-size: 18px;
	}

	@include r($md-breakpoint) {
		height: 152px;
	}

	&:hover,
	&:focus {
		//border: 2px solid #502DFC;

		+ .fieldBorder:before {
			opacity: 1;
		}
	}

	&[aria-invalid="true"] {
		border-color: $red;
	}
}

.form__grid {
	display: flex;
	flex-flow: wrap;

	&--2 {
		margin: 0 -8px;

		> div {
			width: calc(50% - 16px);
			margin: 0 8px;

			@include r($xs-breakpoint) {
				width: 100%;

				&:not(:first-child) {
					margin-top: 21px;
				}
			}
		}
	}
}

.form__field, .form__textarea {
	position: relative;

	.form__label {
		position: absolute;
		top: 0;
		left: 0;
		transition: .3s all;
	}

	.input {
		width: 100%;
	}
}

.form__field {

	&.active {

		.form__label {
			color: #939CAB;
			font-size: 14px;
			top: -20px;
		}

		.input {
			padding-left: 2px;
		}
	}
}

.form__textarea {

	&.active {

		.form__label {
			color: #939CAB;
			font-size: 14px;
			top: -29px;
			left: 0;
		}
	}

	.form__label {
		top: 13px;
		left: 17px;
	}
}

.form__label {
	color: #000;
	line-height: 30px;
	font-size: 21px;
	pointer-events: none;

	@include r($lg-breakpoint) {
		line-height: 26px;
		font-size: 18px;
	}
}

.form {

	> div:not(:first-child) {
		margin-top: 51px;

		@include r($lg-breakpoint) {
			margin-top: 47px;
		}
	}

	div.form__bottom {
		display: flex;
		align-items: center;
		margin: 75px -8px 0;

		@include r($lg-breakpoint) {
			margin-top: 60px;
		}

		@include r($xs-breakpoint) {
			display: block;
			margin-top: 30px;
		}

		.form__button {
			position: relative;

			.form__success {
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-50%);
				opacity: 0;
				visibility: hidden;
			}
		}

		> div {
			margin: 0 8px;
			width: calc(50% - 16px);

			@include r($xs-breakpoint) {
				width: 100%;

				&:not(:first-child) {
					margin-top: 20px;
					padding-right: 20px;
				}
			}
		}
	}

	div.form__upload {

		@include r($lg-breakpoint) {
			margin-top: 24px;
		}
	}
}

.form__rules {
	line-height: 15px;
	font-size: 14px;

	@include r($lg-breakpoint) {
		line-height: 16px;
		font-size: 12px;
	}

	a {
		font-weight: 700;
	}
}

.form__upload {
	display: flex;
	align-items: flex-end;
	margin: 0 -8px;
	justify-content: space-between;

	@include r($xs-breakpoint) {
		display: block;
	}

	> div {
		width: calc(50% - 16px);
		margin: 0 8px;

		@include r($xs-breakpoint) {
			width: 100%;
		}
	}

	.form__description {
		margin-bottom: -5px;

		@include r($xs-breakpoint) {
			margin-bottom: 0;
			margin-top: 20px;
			padding-right: 20px;
		}
	}
}

.form__description {
	line-height: 20px;
	font-size: 14px;
	color: #939CAB;

	@include r($xs-breakpoint) {
		line-height: 17px;
		font-size: 12px;
	}
}

.form__file {
	max-width: 337px;
	min-height: 36px;
	position: relative;

	@include r($lg-breakpoint) {
		max-width: 258px;
	}

	@include r($xs-breakpoint) {
		max-width: 290px;
	}

	&:hover {

		&:after {
			opacity: 1;
		}
	}

	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 1px;
		background-color: #000;
		transition: .3s all;
	}


	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 2px;
		background-color: $primary-color;
		transition: .3s all;
		opacity: 0;
	}

	.file-loader {
		position: absolute;
		bottom: 0;
		left: 0;
		background-color: #502DFC;
		height: 4px;
		pointer-events: none;
	}

	[type="file"] {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		z-index: 2;
		cursor: pointer;
	}

	&-body {
		position: relative;
		line-height: 30px;
		font-size: 21px;
		padding-left: 30px;
		padding-right: 25px;

		@include r($lg-breakpoint) {
			line-height: 26px;
			font-size: 18px;
			padding-left: 32px;
		}

		span {
			word-break: break-all;
		}
	}
}

.file {

	&.is-uploaded {

		&:before {
			bottom: -3px;
			height: 5px;
			background-color: $primary-color;
		}

		.file__remove svg {
			background-color: transparent;
			border: 0;
			border-radius: 0;
			transition: .3s all;
			transform: rotate(45deg);
		}
	}

	svg path, svg rect {
		transition: .3s all;
	}
}

.file:hover,
.file.is-uploaded {

	svg path, svg rect {
		fill: $primary-color;
	}
}

.file__icon {
	position: absolute;
	top: 2px;
	left: -1px;
}

.file__remove {
	top: -5px;
	bottom: 0;
	width: 30px;
	right: 0;
	position: absolute;
	z-index: 2;
}

.form__success {
	@include h5();
	color: $primary-color;
}

.is-error {

	.input, .textarea {
		border-color: $red;
	}
}

.form__error,
.form__textarea .error,
.form__file .error,
.form__field .error {
	position: absolute;
	top: 100%;
	left: 0;
	background: #FFDCDC;
	line-height: 20px;
	font-size: 14px;
	box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.16);
	color: $red;
	display: inline-block;
	padding: 10px 20px;
	font-weight: 600;
	margin-top: 16px;
	z-index: 10;

	&.valid {
		display: none !important;
	}

	&:before {
		content: '';
		position: absolute;
		bottom: 100%;
		left: 0;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 8px 0 0 8px;
		border-color: transparent transparent transparent #FFDCDC;
	}
}

.form__textarea .error {
	top: 40px;
	left: 16px;
}
