.header {
	padding: 41px 0;
	//position: absolute;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	transition: .3s all;
	z-index: 100;

	@include r($lg-breakpoint) {
		padding: 38px 0;
	}

	@include r($md-breakpoint) {
		padding: 34px 0;
	}

	@include r($xs-breakpoint) {
		padding: 18px 0;
	}

	.header__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;

		@include r($xs-breakpoint) {
			z-index: 30;
		}
	}

	.header__right {
		display: flex;
		align-items: center;
		margin-top: -7px;
		padding-right: 3px;

		@include r($lg-breakpoint) {
			margin-top: -3px;
			padding-right: 5px;
		}

		@include r($md-breakpoint) {
			padding-right: 10px;
		}

		@include r($sm-breakpoint) {
			padding-right: 5px;
		}

		@include r($xs-breakpoint) {
			display: none;
		}

		.lang {
			margin-left: 144px;

			@include r($lg-breakpoint) {
				margin-left: 74px;
			}

			@include r($md-breakpoint) {
				margin-left: 89px;
			}

			@include r($sm-breakpoint) {
				margin-left: 50px;
			}
		}
	}

	.header__burger {
		margin-top: -5px;
	}
}

.logo {


	svg {

		@include r($lg-breakpoint) {
			width: 273px;
			height: 15px;
		}

		@include r($md-breakpoint) {
			width: 248px;
		}

		@include r($sm-breakpoint) {
			width: 255px;
		}

		@include r($xs-breakpoint) {
			width: 40px;
			height: 13px;
		}
	}
}

.headerLinks,
.lang {
	@include fz-md();
}

.lang {

	a {
		padding: 10px 0 6px;
		@include hoverEl();
	}
}


.headerLinks {

	a {
		position: relative;
		padding: 10px 0 6px;

		@include hoverEl();
	}
}
