html {
	height: 100%;
}

body {
	//overflow: hidden;

	//overflow-x: hidden;
	//overflow-y: scroll;
}

.out {
	position: relative;
	//overflow: hidden;
	transition: .3s all;
	padding-top: 110px;

	position: absolute;
	width: 100%;
	height: 100%;

	@include r($lg-breakpoint) {
		padding-top: 97px;
	}

	@include r($md-breakpoint) {
		padding-top: 88px;
	}

	@include r($xs-breakpoint) {
		padding-top: 69px;
	}
}

.is-show-cookie {

	.cookie {
		transform: translateY(0%);
		opacity: 1;
		visibility: visible;
	}

	.header {
		top: 72px;

		@include r($lg-breakpoint) {
			top: 56px;
		}

		@include r($sm-breakpoint) {
			top: 72px;
		}

		@include r($xs-breakpoint) {
			top: 110px;
		}
	}

	.out {
		padding-top: 72px + 110px;

		@include r($lg-breakpoint) {
			padding-top: 56px + 97px;
		}

		@include r($sm-breakpoint) {
			padding-top: 72px + 88px;
		}

		@include r($xs-breakpoint) {
			padding-top: 110px + 88px;
		}
	}
}

.no-scroll {
	overflow: hidden;

	@include r($xs-breakpoint) {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
}

.is-fixed {
	position: fixed;
	top: 0;
	left: 0;
}

.section--gray {
	background-color: #F6F6F6;
}

.section__title {
	@include h2();

	span {
		display: block;
		color: $primary-color;
	}

	&--lg {
		@include h1();
	}
}

.section__title-sm {
	@include h2();
}

.section__text {
	@include fz-text();

	&.section__text--xs {
		@include fz-text-sm();
	}
}

.section__description {
	@include h4();
}

.animated-words {
	position: relative;

	@include r($xs-breakpoint) {
		display: inline-block;
	}

	span {
		opacity: 0;
		transform: translateY(3px);
	}

	span:not(:first-child) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #fff;
	z-index: 100;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: .3s all;

	svg {
		overflow: visible;
	}
}

.red-top {
	padding-top: 128px;

	@include r($lg-breakpoint) {
		padding-top: 88px;
	}

	@include r($xs-breakpoint) {
		padding-top: 64px;
	}
}

.red-bottom {
	padding-bottom: 128px;

	@include r($lg-breakpoint) {
		padding-bottom: 88px;
	}

	@include r($xs-breakpoint) {
		padding-bottom: 64px;
	}
}

.green-top {
	padding-top: 88px;

	@include r($xs-breakpoint) {
		padding-top: 64px;
	}
}

.orange-bottom {
	padding-bottom: 168px;

	@include r($xs-breakpoint) {
		padding-bottom: 64px;
	}
}

.page {
	//transition: .3s all;
	//opacity: 0;
	//visibility: hidden;
	position: absolute;
	overflow: visible;
	min-height: 100%;
	width: 100%;
	//top: 0;
	//left: 0;
	//right: 0;
	//bottom: 0;

	.content {
		position: absolute;
		width: 100%;
	}
}

.page.is-coming .content {
	//animation: mainContentIncoming 1.2s cubic-bezier(.86, 0, .07, 1) both
}

.page.is-leaving .content {
	animation: mainContentLeaving 1.2s cubic-bezier(.86, 0, .07, 1) both
}

.content {
	position: relative;
}

.page.is-active,
.page.is-coming {
	opacity: 1;
	visibility: visible;

	&.is-leaving {
		opacity: 0;
		visibility: hidden;
	}
}

@keyframes mainOverlayIncoming {
	from {
		top: 0;
		bottom: 0
	}

	to {
		top: 0;
		bottom: 100%
	}
}

@keyframes mainOverlayLeaving {
	from {
		//top: 100%;
		//bottom: 0;
		opacity: 1;
	}

	to {
		//top: 0;
		//bottom: 0;
		opacity: 0;
	}
}

@keyframes mainContentIncoming {
	from {
		transform: translateY(50%)
	}

	to {
		transform: translateY(0)
	}
}

@keyframes mainContentLeaving {
	from {
		//transform: translateY(0);
		opacity: 1;

	}

	to {
		//transform: translateY(-50%);
		opacity: 0;
	}
}

.line-scale-party>div:nth-child(1) {
	animation-delay: .48s;
	animation-duration: .54s
}

.line-scale-party>div:nth-child(2) {
	animation-delay: -.15s;
	animation-duration: 1.15s
}

.line-scale-party>div:nth-child(3) {
	animation-delay: 40ms;
	animation-duration: .77s
}

.line-scale-party>div:nth-child(4) {
	animation-delay: -.12s;
	animation-duration: .61s
}

.line-scale-party>div {
	animation-fill-mode: both;
	animation-name: line-scale-party;
	animation-iteration-count: infinite;
	animation-delay: 0
}

.line-scale-party>div {
	width: 4px;
	height: 35px;
	display: inline-block;
	border-radius: 2px;
	margin: 2px;
	background-color: $primary-color;
}

@keyframes line-scale-party {
	0%,100% {
		-webkit-transform: scale(1);
		transform: scale(1)
	}

	50% {
		-webkit-transform: scale(.5);
		transform: scale(.5)
	}
}

.js-scrollbar {
	position: absolute !important;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}
