.section--about {
	padding-top: 80px;
	padding-bottom: 145px;

	@include r($lg-breakpoint) {
		padding-top: 52px;
		padding-bottom: 105px;
	}

	@include r($md-breakpoint) {
		padding-top: 48px;
	}
	
	@include r($sm-breakpoint) {
		padding-top: 52px;
	}
}

.about {

	&__title {
		@include h1();
		max-width: 513px;
		position: relative;
		z-index: 1;
	}

	&__photo {
		width: percentage(1694/1900);
		height: 680px;
		margin-right: -100px;
		margin-left: auto;
		margin-top: -54px;
		@include cover();

		@include r($lg-breakpoint) {
			margin-top: -30px;
			width: percentage(1255/1360);
			margin-right: -100px;
			height: 520px;
		}

		@include r($md-breakpoint) {
			height: 392px;
			margin-top: -34px;
			margin-right: -80px;
			width: percentage(953/1024);
		}

		@include r($sm-breakpoint) {
			height: 280px;
			margin-right: -60px;
			margin-top: -38px;
		}
	}

	&__text {
		@include fz-text-sm();
		max-width: 1088px;
		margin-top: 108px;

		@include r($lg-breakpoint) {
			margin-top: 70px;
			max-width: 770px;
		}
		
		@include r($xs-breakpoint) {
			line-height: 26px;
			font-size: 18px;
		}

		p:not(:first-child) {
			margin-top: 22px;

			@include r($lg-breakpoint) {
				margin-top: 15px;
			}
		}
	}
}
