html {
	font-size: 16px;
	font-family: $font-main;
	-webkit-tap-highlight-color: transparent;
	@include font-smoothing();
}

h1, h2, h3, h4 {

}

h1 {

}

h2 {

}

h3 {

}

h4 {

}

h5 {
	@include h5();
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

a {
	transition: .3s all ease;
	text-decoration: none;
	color: $black;

	&:hover {
		color: $primary-color;
	}
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

input:focus {
	outline: none;
}

::selection {
	color: #000;
	background-color: #C2C8FF;
}

::-moz-selection {
	color: #000;
	background-color: #C2C8FF;
}

article {

	p {
		margin: 33px 0;

		@include r($lg-breakpoint) {
			margin: 23px 0;
		}

		@include r($xs-breakpoint) {
			margin: 15px 0;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}
