.section--principles {
	padding-top: 90px;
	padding-bottom: 145px;

	@include r($lg-breakpoint) {
		padding-top: 64px;
		padding-bottom: 100px;
	}

	@include r($xs-breakpoint) {
		padding-top: 50px;
		padding-bottom: 64px;

	}


	.principles {
		margin-top: 50px;

		@include r($lg-breakpoint) {
			margin-top: 24px;
		}
	}

	article {

		p {
			@include fz-text-sm();
			margin: 22px 0;

			@include r($lg-breakpoint) {
				margin: 14px 0;
			}
			
			@include r($xs-breakpoint) {
				line-height: 26px;
				font-size: 18px;
			}
		}
	}
}

.principles {
	display: flex;

	&__left {
		width: percentage(1196/1588);

		@include r($lg-breakpoint) {
			width: percentage(715/1079);
		}

		@include r($md-breakpoint) {
			width: 100%;
		}
	}

	&__right {
		width: percentage(278/1588);


		@include r($lg-breakpoint) {
			width: percentage(256/1079);
		}

		@include r($md-breakpoint) {
			display: none;
		}
	}
}
