.section--steps {
	padding-top: 80px;
	padding-bottom: 145px;

	@include r($lg-breakpoint) {
		padding-top: 52px;
		padding-bottom: 100px;
	}

	@include r($sm-breakpoint) {
		padding-top: 45px;
	}

	@include r($xs-breakpoint) {
		padding-bottom: 64px;
	}

	.stepsList {
		margin-top: 76px;
		max-width: 1196px;

		@include r($lg-breakpoint) {
			margin-top: 55px;
			max-width: 897px;
		}

		@include r($sm-breakpoint) {
			margin-top: 65px;
		}

		@include r($xs-breakpoint) {
			margin-top: 57px;
		}
	}
}

.stepsList {
	counter-reset: list;
}



.stepsList__item {
	position: relative;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 73px;
		width: 2px;
		background-color: #EDEDED;
		bottom: -47px;

		@include r($lg-breakpoint) {
			left: 53px;
		}

		@include r($sm-breakpoint) {
			left: 38px;
		}

		@include r($xs-breakpoint) {
			left: 14px;
		}

	}

	&:last-child {
		overflow: hidden;

		.steps__body {

			li {

				&:last-child {
					&:after {
						content: '';
						position: absolute;
						top: 15px;
						left: -45px;
						width: 10px;
						background-color: #fff;
						height: 150%;
						z-index: 2;

						@include r($xs-breakpoint) {
							left: -30px;
						}
					}
				}
			}
		}
	}

	&:not(:first-child) {
		margin-top: 47px;

		@include r($xs-breakpoint) {
			margin-top: 22px;
		}
	}

	&:before {
		counter-increment: list;
		content: counter(list);
		position: absolute;
		top: 25px;
		left: 65px;
		@include h3();
		color: $primary-color;
		z-index: 2;

		@include r($lg-breakpoint) {
			left: 46px;
			top: 20px;
		}

		@include r($sm-breakpoint) {
			top: 15px;
			left: 31px;
		}

		@include r($xs-breakpoint) {
			content: none;
		}
	}

	&.is-active {

		.steps__head {
			border-color: #502DFC;
			background-color: #fff;

			&:before {
				transform: translateY(-50%) rotate(180deg);
			}
		}
	}
}

.steps {
	
	&__head {
		background-color: #F6F6F6;
		@include h4();
		padding: 36px 137px 31px;
		border: 2px solid transparent;
		border-radius: 4px;
		position: relative;
		transition: .3s all;
		z-index: 1;
		cursor: pointer;
		
		@include r($lg-breakpoint) {
			padding: 28px 90px 25px;
		}
		
		@include r($sm-breakpoint) {
			padding: 28px 72px 25px;
		}

		@include r($xs-breakpoint) {
			min-height: 80px;
			padding: 16px 60px 10px 38px;
			display: flex;
			align-items: center;
		}
		
		&:hover {
			background-color: #F3F1FF;
		}

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			right: 46px;
			margin-top: -1px;
			transform: translateY(-50%);
			width: 20px;
			height: 11px;
			background-image: url('../img/arrow-down.svg');
			@include cover();
			transition: .3s all;

			@include r($lg-breakpoint) {
				right: 25px;
			}

			@include r($xs-breakpoint) {
				right: 14px;
			}
		}
	}

	&__body {
		padding: 86px 140px 45px;

		@include r($lg-breakpoint) {
			padding: 47px 92px 15px;
		}

		@include r($sm-breakpoint) {
			padding: 47px 72px 15px;
		}

		@include r($xs-breakpoint) {
			padding: 35px 0 35px 40px;
		}

		p {
			@include fz-text-sm();
			margin: 11px 0;

			@include r($lg-breakpoint) {
				margin: 10px 0;
			}
		}

		ul {

			@include r($lg-breakpoint) {
				max-width: 670px;
			}

			@include r($sm-breakpoint) {
				max-width: 389px;
			}
		}

		li {
			position: relative;

			&:before {
				content: '';
				position: absolute;
				top: 5px;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: #EDEDED;
				left: -71px;

				@include r($lg-breakpoint) {
					left: -43px;
				}

				@include r($sm-breakpoint) {
					left: -38px;
				}

				@include r($xs-breakpoint) {
					left: -30px;
				}

			}

			&:not(:first-child) {
				margin-top: 50px;

				@include r($lg-breakpoint) {
					margin-top: 47px;
				}

				@include r($xs-breakpoint) {
					margin-top: 31px;
				}
			}
		}
	}
}
