// Colors
$primary-color: #502DFC;
$primary-color-lighten: #7154FE;
$primary-color-light: #71C87E;

$black: #323232;
$black-lighten: #595959;

$white: #fff;

$red: #FF0000;

// Box shadow
$box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.05);

// Border radius
$border-radius: 40px;

// Transition
$easing: cubic-bezier(0.58, 0.01, 0.41, 1);
$transition: all 0.3s $easing;

// Text shadow
$text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.4);

// Breakpoints
$xlg-breakpoint: 1400;
$lg-breakpoint: 1600;
$md-breakpoint: 1100;
$sm-breakpoint: 900;
$xs-breakpoint: 767;

$mob: $xs-breakpoint;

$levels: (
		//      320    768   1024  1920
		yellow: (64px, 64px, 64px, 64px),
		green:  (88px, 88px, 88px, 88px),
		red:    (128px, 128px, 128px, 128px),
		orange: (168px, 168px, 168px, 168px),
);
