.section--article {
	padding: 106px 0 123px;

	@include r($lg-breakpoint) {
		padding: 78px 0 80px;
	}

	@include r($md-breakpoint) {
		padding: 78px 0 108px;
	}
	
	@include r($sm-breakpoint) {
		padding: 78px 0 80px;
	}

	@include r($xs-breakpoint) {
		padding: 0 0 27px;
	}
}

.article {
	display: flex;
	justify-content: space-between;

	@include r($md-breakpoint) {
		display: block;
	}

	@include r($xs-breakpoint) {
		padding: 53px 0 64px;
	}

	&__left {
		width: percentage(358/1600);

		@include r($md-breakpoint) {
			width: 100%;
			display: flex;
			align-items: flex-end;
			justify-content: space-between;
		}

		@include r($xs-breakpoint) {
			display: block;
		}

		.section__title {
			margin-bottom: 103px;

			@include r($lg-breakpoint) {
				margin-bottom: 106px;
			}

			@include r($md-breakpoint) {
				margin-bottom: 0;
				max-width: 238px;
			}

			@include r($xs-breakpoint) {
				max-width: 100%;
				margin-bottom: 40px;
			}
		}

		.btn {

			@include r($md-breakpoint) {
				position: relative;
				top: -7px;
				margin-right: 7px;
			}

			@include r($sm-breakpoint) {
				margin-right: 2px;
			}

			@include r($xs-breakpoint) {
				position: absolute;
				bottom: 0;
				left: 16px;
				top: auto;
			}
		}
	}

	&__right {
		width: percentage(1195/1600);
		margin-top: -30px;

		@include r($lg-breakpoint) {
			margin-top: 7px;
		}

		@include r($md-breakpoint) {
			width: 100%;
			margin-top: 48px;
		}

		@include r($xs-breakpoint) {
			margin-top: 15px;
		}

		.card--article {
			padding-top: 40px;

			@include r($lg-breakpoint) {
				padding-top: 11px;
			}

			@include r($md-breakpoint) {
				padding-top: 0;
			}

			.card__title {
				max-width: 398px;

				@include r($md-breakpoint) {
					max-width: 438px;
				}

				@include r($sm-breakpoint) {
					max-width: 304px;
				}
			}

			.card__text {
				max-width: 506px;

				@include r($lg-breakpoint) {
					max-width: 376px;
				}

				@include r($md-breakpoint) {
					max-width: 422px;
				}

				@include r($sm-breakpoint) {
					max-width: 304px;
				}
			}
		}
	}
}

.card--article {
	position: relative;

	@include r($xs-breakpoint) {
		display: flex;
		flex-flow: column-reverse;
	}

	.card__photo {
		width: 523px;
		height: 360px;
		@include cover();
		position: absolute;
		top: 0;
		right: 0;

		@include r($lg-breakpoint) {
			width: 349px;
			height: 240px;
		}

		@include r($md-breakpoint) {
			width: 256px;
			height: 197px;
			right: 4px;
		}

		@include r($sm-breakpoint) {
			width: 236px;
			height: 180px;
			right: 0;
			top: 32px;
			margin: 0 auto 20px;
		}

		@include r($xs-breakpoint) {
			position: relative;
			width: 282px;
			height: 223px;
			order: 0;
			top: 0;
		}
	}

	.card__title {
		@include h3();
		line-height: 46px;
		color: $primary-color;
		display: block;

		@include r($xs-breakpoint) {
			order: 0;
		}

		&:hover {

			span {
				background-size: .9em 1.5em;
				color: #fff;
			}
		}

		span {
			background-image: linear-gradient($primary-color,$primary-color);
			background-repeat: repeat-x;
			background-size: 0 0;
			background-position: 0 65%;
			transition: .3s all;
		}

	}

	.card__text {
		margin-top: 35px;
		@include fz-text-sm();

		@include r($md-breakpoint) {
			margin-top: 28px;
		}

		@include r($sm-breakpoint) {
			margin-top: 20px;
		}

		@include r($xs-breakpoint) {
			order: -1;
			line-height: 26px;
			font-size: 18px;
			margin-top: 15px;
		}
	}
}
