.section--blockqoute {
	padding: 107px 0 122px;

	@include r($lg-breakpoint) {
		padding: 78px 0 87px;
	}

	@include r($xs-breakpoint) {
		padding: 55px 0 63px;
	}

	.section__title-sm {
		max-width: 1343px;

		@include r($lg-breakpoint) {
			max-width: 971px;
		}

		@include r($md-breakpoint) {
			max-width: 714px;
		}
	}

	.section__description {
		margin-top: 63px;

		@include r($lg-breakpoint) {
			margin-top: 47px;
		}
	}
}
