.card--work {
	position: relative;

	&:hover {

		.cardPhoto .cardPhoto__action {
			opacity: 1;
		}
	}

	&.is-unloaded {
		//background: #fff url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='481' height='42' viewBox='0 0 481 42'>  <rect fill='%23f6f6f6' y='27' width='130' height='9' rx='2' ry='2'/>  <rect fill='%23f6f6f6' y='7' width='70' height='10' rx='2' ry='2'/>  <!--<circle fill='none' stroke='%23f6f6f6' stroke-width='2px' cx='460' cy='21' r='20'/>--></svg>") 50% 94% no-repeat;
		background: url('data:image/svg+xml;base64,') no-repeat;
        background-size: contain;
		min-height: 310px;

		.card__inner {
			opacity: 0;
			visibility: hidden;
		}
	}

	&.card--lg {

		&.is-unloaded {
			min-height: 708px;
			background: url('data:image/svg+xml;base64,') no-repeat;
			background-size: contain;
		}

		.cardPhoto {
			height: 600px;

			@include r($lg-breakpoint) {
				height: 405px;
			}

			@include r($md-breakpoint) {
				height: 301px;
			}

			@include r($sm-breakpoint) {
				height: 307px;
			}

			@include r($xs-breakpoint) {
				height: 224px;
			}
		}

		.card__body {
			padding: 15px 0;

			@include r($md-breakpoint) {
				padding: 10px 0;
			}

			@include r($xs-breakpoint) {
				padding: 12px 0;
			}
		}

		.card__title {
			@include h3();
		}

		.card__subtitle {
			margin-top: 7px;

			@include r($md-breakpoint) {
				margin-top: 0;
			}

			@include r($xs-breakpoint) {
				margin-top: 6px;
			}
		}
	}

	.card__link {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 3;
	}

	.cardPhoto {
		position: relative;
		height: 232px;

		@include r($lg-breakpoint) {
			height: 157px;
		}

		@include r($md-breakpoint) {
			height: 117px;
		}

		@include r($sm-breakpoint) {
			height: 114px;
		}

		@include r($xs-breakpoint) {
			height: 160px;
		}

		> * {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			@include cover();
		}
		
		.cardPhoto__action {
			background-color: $primary-color;
			z-index: 2;
			position: absolute;
			opacity: 0;
		}
	}

	.card__body {
		padding: 13px 0;

		@include r($md-breakpoint) {
			padding: 10px 0;
			margin: 0 -5px;
		}

		@include r($sm-breakpoint) {
			margin: 0;
		}

		@include r($xs-breakpoint) {
			padding: 14px 0;
		}
	}

	.card__title {
		@include h4();
	}

	.card__subtitle {
		line-height: 23px;
		font-size: 16px;
		margin-top: 3px;
		color: #A0A0A0;
		
		@include r($md-breakpoint) {
			line-height: 20px;
			font-size: 14px;
			margin-top: -2px;
		}

		@include r($xs-breakpoint) {
			text-transform: uppercase;
			margin-top: 4px;
			font-size: 12px;
		}
	}
}
