@mixin font-smoothing() {
	text-rendering: optimizelegibility;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-tap-highlight-color: rgba(0, 0, 0, 0);
	-moz-text-size-adjust: none;
	-moz-font-smoothing: antialiased;
	-ms-tap-highlight-color: rgba(0, 0, 0, 0);
	-ms-text-size-adjust: none;
	-ms-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin top-menu-items-transition($items: 5, $initial-transition-delay: 100ms, $next-transition-delay: 50ms) {
	@for $i from 0 through $items {
		&:nth-child(#{$i + 1}) {
			transition-delay: (
					$initial-transition-delay + ($next-transition-delay * $i)
			),
					($initial-transition-delay + ($next-transition-delay * $i));
		}
	}
}

@mixin fz-text() {
	line-height: 37px;
	font-size: 26px;
	
	@include r($lg-breakpoint) {
		line-height: 32px;
		font-size: 22px;
	}
	
	@include r($xs-breakpoint) {
		line-height: 26px;
		font-size: 18px;
	}
}

@mixin fz-text-sm() {
	line-height: 30px;
	font-size: 21px;

	@include r($lg-breakpoint) {
		line-height: 26px;
		font-size: 18px;
	}
	
	@include r($xs-breakpoint) {
		line-height: 20px;
		font-size: 14px;
	}
}

@mixin fz-lg() {
	line-height: 26px;
	font-size: 26px;
	font-weight: 700;
	
	@include r($lg-breakpoint) {
		line-height: 25px;
		font-size: 22px;
	}
}

@mixin fz-md() {
	line-height: 26px;
	font-size: 16px;
	letter-spacing: 0.05em;
	font-weight: 700;
	text-transform: uppercase;
	
	@include r($lg-breakpoint) {
		line-height: 23px;
		font-size: 14px;
		letter-spacing: 0.05em;
	}
}

@mixin h1() {
	font-weight: 900;
	line-height: 96px;
	font-size: 96px;
	
	@include r($lg-breakpoint) {
		line-height: 81px;
		font-size: 80px;
	}
	
	@include r($xs-breakpoint) {
		line-height: 60px;
		font-size: 60px;
	}
}

@mixin h2() {
	font-weight: 900;
	line-height: 73px;
	font-size: 72px;
	
	@include r($lg-breakpoint) {
		line-height: 57px;
		font-size: 60px;
	}
	
	@include r($xs-breakpoint) {
		line-height: 41px;
		font-size: 40px;
	}
}

@mixin h3() {
	font-weight: 900;
	line-height: 48px;
	font-size: 38px;

	@include r($lg-breakpoint) {
		line-height: 41px;
		font-size: 32px;
	}

	@include r($xs-breakpoint) {
		line-height: 25px;
		font-size: 22px;
	}
}

@mixin h4() {
	font-weight: 700;
	line-height: 26px;
	font-size: 26px;

	@include r($lg-breakpoint) {
		line-height: 25px;
		font-size: 22px;
	}
}

@mixin h5() {
	font-weight: 700;
	line-height: 26px;
	font-size: 16px;
	text-transform: uppercase;
	letter-spacing: 0.05em;

	@include r($lg-breakpoint) {
		line-height: 23px;
		font-size: 14px;
	}
}

@mixin hoverEl() {
	background-image: linear-gradient($primary-color,$primary-color);
	background-repeat: repeat-x;
	background-size: 0 0;
	background-position: 0 65%;

	&:hover {
		background-size: .9em 1.5em;
		color: #fff;
	}
}
