.work__block--dialog {
}

.dialog {

	&__item {
		display: flex;

		@include r($xs-breakpoint) {
			padding-left: 22px;
		}

		&:not(:first-child) {
			margin-top: 90px;

			@include r($lg-breakpoint) {
				margin-top: 63px;
			}

			@include r($sm-breakpoint) {
				margin-top: 65px;
			}

			@include r($xs-breakpoint) {
				margin-top: 24px;
			}
		}

		&.dialog__item--reverse {
			flex-flow: row-reverse;

			@include r($xs-breakpoint) {
				padding-left: 0;
				padding-right: 22px;
			}

			.dialog__body {

				@include r($sm-breakpoint) {
					margin-left: 0;
					margin-right: auto;
				}
			}

			.dialog__photo {
				margin-left: 65px;
				margin-right: 0;

				@include r($lg-breakpoint) {
					margin-left: 58px;
				}
			}

			.triangle {
				right: auto;
				left: 100%;

				@include r($xs-breakpoint) {
					margin-right: 0;
					margin-left: -3px;
				}
			}
		}
	}

	&__photo {
		border: 4px solid #FFFFFF;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
		min-width: 158px;
		height: 158px;
		border-radius: 100%;
		@include cover();
		margin-right: 65px;

		@include r($lg-breakpoint) {
			min-width: 120px;
			height: 120px;
			margin-right: 62px;
		}

		@include r($sm-breakpoint) {
			display: none;
		}
	}

	&__title {
		@include h4();
	}

	&__text {
		font-weight: 600;
		line-height: 31px;
		font-size: 21px;
		margin-top: 17px;
		margin-left: -2px;
		
		@include r($lg-breakpoint) {
			line-height: 26px;
			font-size: 18px;
			margin-top: 11px;
		}

		@include r($sm-breakpoint) {
			margin-top: 13px;
		}

		@include r($xs-breakpoint) {
			font-size: 14px;
			line-height: 20px;
			font-weight: normal;
			margin-left: 0;
			margin-top: 8px;
		}
	}

	&__body {
		display: inline-block;
		padding: 43px 52px 60px;
		box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.04);
		border-radius: 10px;
		max-width: 838px;
		position: relative;

		@include r($lg-breakpoint) {
			max-width: 624px;
			padding: 30px 40px 50px;
		}

		@include r($sm-breakpoint) {
			max-width: 545px;
			margin-left: auto;
			padding: 30px 40px 22px;
		}

		@include r($xs-breakpoint) {
			padding: 22px 15px 25px;
		}

		.triangle {
			position: absolute;
			top: 30px;
			right: 100%;

			@include r($xs-breakpoint) {
				top: 12px;
				margin-right: -3px;

			    svg {
				    width: 24px;
				    height: 13px;
			    }
			}
		}
	}
}
